import React, { useRef } from "react";
import NewsTicker, {
  Directions,
  TickerStates,
} from "react-advanced-news-ticker";
import { Link } from "react-router-dom";
import "./news.scss";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
const N = ({ words }) => {
  const newsTickerRef = useRef(null);
  return (
    <div className="news">
         -   /
      <NewsTicker
        ref={newsTickerRef}
        autoStart={true}
        maxRows={1}
        pauseOnHover={true}
        speed={400}
        duration={3000}
      >
        {words.map((el) => (
          <Link to={`/news/${el._id}`}>{el.title}</Link>
        ))}
      </NewsTicker>
      <div
        className="action"
        onClick={() => {
          newsTickerRef.current.unPause();
        }}
      >
        <PlayArrowIcon />
      </div>
      <div
        className="action"
        onClick={() => newsTickerRef.current.pause()}
      >
        <PauseIcon />
      </div>
    </div>
  );
};

export default N;
